оты с вами, чем от простой беготни и игр с другими собаками.\n      Мы основываемся на бесконфликтной системе дрессировки. Эффективной методике оперантного обучения, задействуем природные\n      инстинкты собаки и ее инициативу, подкрепляя правильное поведение наградой.\n      Выполняя упражнение собака получает желаемое.\n      Таким образом с каждой тренировкой мы с Вами получаем максимальный результат, а собаки удовольствие и так называемую\n      «зарплату» за проделанную работу.")]),_c('p',{staticClass:"simpleTextBlock"},[_vm._v("\n      Культивированным стереотипом является мнение что собак нужно обучать с четырёх, шести месяцев и даже с одного года,\n      по нашему мнению это не совсем так. Мы начинаем тренироваться со щенками в два с половиной — три месяца и\n      придерживаемся мнения, что обучение начинается с первого дня присутствия собаки в доме и выражается оно в элементарных\n      действиях, которые мы предоставляем в виде игры, к примеру — когда мы подзываем щенка по кличке, которая для него\n      еще не знакома, давая в замен желаемое, проявляя ласку и теплое отношение, щенок конечно же радостно отвечает взаимностью,\n      бежит и облизывает вам все лицо. В ближайшие недели этот щенок будет само выражаться через положительно подкрепленное вами\n      поведение и довольно скоро будет готов научится чему-то новому, например, таким командам, как: «ко мне», «сидеть», «лежать»,\n      «стоять». Которые в перспективе через некоторое время будут прогрессировать в более сложные элементы : «Ждать», «Рядом»,\n      «Охранять», «Нет», «На место», «Апорт».")]),_c('p',{staticClass:"simpleTextBlock"},[_vm._v("\n      Таким образом Ваш пес обучается позитивно, прогрессивно и что самое главное с желанием!\n      Также мы должны согласится что, если не занимать щенка с детства, то его весь потенциал уходит, например, на такие занятия\n      как подбирание пищи, погони за велосипедистами, котами, погрызенная обувь, мебель и прочие «радости».\n      Удовлетворяя с